* {
  box-sizing: border-box;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
}

body, h1, h2, p {
  margin: 0;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  margin: 0 auto 40px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__product-picture {
  justify-content: center;
  width: 166px;
  height: 134px;
  margin-bottom: 40px;
  display: flex;
}

.card__product-picture_photo {
  width: 160px;
  height: 134px;
}

.card__product-name {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  margin-bottom: 24px;
  display: flex;
}

.card__reviews {
  text-align: right;
  color: #060b35;
  margin-top: 2px;
  font-size: 10px;
  line-height: 14px;
}

.card__product-price {
  justify-content: space-between;
  margin-bottom: 16px;
  display: flex;
}

.card__product-price-text {
  text-align: left;
  color: #616070;
  line-height: 18px;
}

.card__product-price-price {
  text-align: right;
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #fff;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border-color: #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
